@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Lora:400italic);
@import url(https://fonts.googleapis.com/css?family=Fredericka+the+Great);
@import url(https://fonts.googleapis.com/css?family=Gugi);
body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  background-color: #c8cdef; }

.event-name {
  margin: 4vh;
  font-size: 2rem;
  color: #222;
  font-family: "Montserrat", sans-serif; }

.event-name span {
  color: #24307a;
  font-family: "Gugi", cursive;
  font-size: 5rem;
  word-spacing: 1rem;
  letter-spacing: 1rem; }

.countdown {
  margin: 6vh auto;
  color: #fff; }

.countdown-col {
  display: inline-block;
  font-family: "Gugi", cursive; }

.countdown-col-element {
  width: 250px;
  display: inline-block;
  padding: 2vw;
  margin: 2vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border: 4px solid #24307a;
  border-radius: 1.5rem;
  background-color: #5262cb; }

.countdown-col-element p {
  padding: 3vh;
  margin: 1vw;
  font-size: 5rem;
  font-family: "Gugi", cursive;
  border: 4px solid #fff;
  border-radius: 1rem;
  background-color: #24307a; }

.countdown-col-element span {
  font-size: 2rem; }

.chooser {
  font-size: 1.5rem;
  font-family: "Montserrat", sans-serif;
  color: #222;
  padding: 5vh; }

.chooser-buttons {
  margin: 0 auto;
  width: 60vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around; }

button {
  color: #222;
  background-color: transparent;
  font-size: 1rem;
  font-family: "Montserrat", sans-serif;
  border: 4px solid #5262cb;
  border-radius: 1.6rem;
  padding: 1vh 2vw;
  text-align: center; }

button:hover {
  background-color: #5262cb;
  color: #fff;
  cursor: pointer; }

button:focus {
  outline: none; }


